<template>
    <div class="tab_bar_item" @click='itemClick'>
       
        <div><slot name='item-text'></slot></div>
        
        
        </div>
</template>

<script>
export default {
  name: 'tabbarItem',
  props:{
    path:String,
  },
  data() { 
    return {

    }
  },
  methods:{
    itemClick(){
      //home -> item1(/home)  = true
      //cart -> item1(/home)  = false
      //category -> item1(/home)  = false
      //profile -> item1(/home)  = false
      return this.$router.push(this.path)
    }
  }
 }
</script>

<style lang="" scoped>
  .tab_bar_item {
    flex: 1;
    text-align: center;
    height: 50px;
    line-height: 50px;
    font-size: 14px;
    color: white;
  }
</style>


